import { Component, OnInit } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { Location } from '@angular/common';
import { Store } from '@ngxs/store';
import { UtilsService } from '../../@core/utils.service';
import { PopoverController } from '@ionic/angular';
import { InfoPopoverComponent } from '../../@shared/components/info-popover/info-popover.component';
import { OrganizationTypes } from '../organization-types';
import { OrganizationBasePage } from '../organization-base.page';
import { StorageService } from '../../@core/storage.service';
import { TranslateService } from '@ngx-translate/core';
import { UpdateCurrentPageData } from '../../@store/pages/pages.actions';
import { Camera } from '@ionic-native/camera/ngx';
import { InformService } from '../../@core/inform.service';

const PAGE_POSITION = 0;

@Component({
  selector: 'app-type-organization',
  templateUrl: './type-organization.page.html',
  styleUrls: ['./type-organization.page.scss'],
})
export class TypeOrganizationPage extends OrganizationBasePage implements OnInit {
  public selectedItem = OrganizationTypes.None;

  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    camera: Camera,
    informService: InformService,
    storage: StorageService,
    private popoverController: PopoverController,
    private translateService: TranslateService
  ) {
    super(location, router, store, utilsService, camera, informService, storage);
  }

  ngOnInit() {}

  public selectItem(id): void {
    if (this.isItemSelected(id)) {
      this.selectedItem = OrganizationTypes.None;
    } else {
      this.selectedItem = id;
      this.goSubtypePage();
    }
  }

  public isItemSelected(id): boolean {
    return this.selectedItem === id;
  }

  public async goSubtypePage(): Promise<void> {
    this.store.dispatch(new UpdateCurrentPageData({ organization_type: this.selectedItem }));

    const navigationExtras: NavigationExtras = {
      state: {
        organizationType: this.selectedItem,
      },
    };

    this.goNextFlowPage(PAGE_POSITION, navigationExtras);
  }

  private getOption(option) {
    switch (option) {
      case OrganizationTypes.Sport:
        return OrganizationTypes.Sport;
      case OrganizationTypes.Academic:
        return OrganizationTypes.Academic;
      case OrganizationTypes.Other:
        return OrganizationTypes.Other;
      default:
        console.error('No type selected');
        break;
    }
  }

  async presentPopover($event, option) {
    const title = this.translateService.instant(`type-organization.option${this.getOption(option)}`);
    const text = this.translateService.instant(`type-organization.help${this.getOption(option)}`);

    const popover = await this.popoverController.create({
      event: $event,
      component: InfoPopoverComponent,
      cssClass: 'custom-alert',
      translucent: true,
      componentProps: { title, text },
    });
    return await popover.present();
  }
}
